
import Swiper from 'vux/src/components/swiper/swiper.vue'
import Grid from 'vux/src/components/grid/grid.vue'
import GridItem from 'vux/src/components/grid/grid-item.vue'

export default{
  components: {
    Swiper,
    Grid,
    GridItem
  },
  created () {
    this.listSwipe()
  },
  data () {
    return {
      swipe: [{
        url: 'javascript:',
        img: 'https://static.vux.li/demo/1.jpg',
        title: '送你一朵fua'
      }, {
        url: 'javascript:',
        img: 'https://static.vux.li/demo/2.jpg',
        title: '送你一辆车'
      }, {
        url: 'javascript:',
        img: 'https://static.vux.li/demo/3.jpg',
        title: '送你一次旅行'
      }]
    }
  },
  methods: {
    listSwipe () {
      this.$vux.loading.show({})
      this.$http.post('/api/query/listQuery').then((response) => {
        let res = response.body
        this.$vux.loading.hide()
        if (res.status === 10000) {
          this.swipe = res.swipe
        } else {
          this.$vux.toast.show({
            type: 'text',
            position: 'bottom',
            text: '轮播图获取失败，请稍候再试'
          })
        }
      }, () => {
        this.$vux.loading.hide()
        this.$vux.toast.show({
          type: 'text',
          position: 'bottom',
          text: '轮播图获取失败，请稍候再试'
        })
      })
    }
  }
}



