import Vue from 'vue'
import Router from 'vue-router'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'home',
      components: {
        main: require('../components/home/home.vue'),
        navbar: require('../components/common/tabbar.vue')
      }
    },
    {
      path: '/message',
      name: 'message',
      components: {
        main: require('../components/message/message.vue'),
        navbar: require('../components/common/tabbar.vue')
      }
    },
    {
      path: '/user',
      name: 'user',
      components: {
        main: require('../components/user/user.vue'),
        navbar: require('../components/common/tabbar.vue')
      }
    }
  ]
})



